<template>
  <div>
    <van-search
      v-model="searchValue"
      placeholder="请输入搜索关键词"
    />
    <div class="new-business">
      <router-link :to="{path:'/add-unit',query:{hotelId:$route.query.hotelId}}"> + 添加协议单位</router-link>
    </div>
    <div class="business-group">
      <div
        class="business-item g_flex"
        v-for="item in businessList"
        :key="item.id"
        @click="goNext('/agreement-details',item)"
      >
        <div class="g_left">
          <div class="g_fw">{{ item.cooperationName }}</div>
          <div class="g_c_98">{{ item.contact }} {{ item.phone }}</div>
        </div>
        <div class="g_c_98">{{ item.roomNum }}间</div>
      </div>
    </div>
  </div>
</template>

<script>
import { GetBusinessAccountUnitbyhotel } from '@/api/api_agreement';
export default {
    name: 'index',
    data() {
        return {
            businessList: [],
            searchValue: null
        };
    },
    watch: {
        searchValue(neww) {
            this.getBusinessCoop(neww);
        }
    },
    created() {
        this.$store.commit('setHeader', '协议单位');
    },
    methods: {
        //查询单位协议
        getBusinessCoop(val) {
            GetBusinessAccountUnitbyhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                if (!val) {
                    this.businessList = res.data.sort(function (a, b) {
                        return b.roomNum - a.roomNum;
                    });
                } else {
                    this.businessList = res.data
                        .filter((item) => {
                            if (item.cooperationName.indexOf(val) != -1) {
                                return item;
                            }
                        })
                        .sort(function (a, b) {
                            return b.roomNum - a.roomNum;
                        });
                }
            });
        },
        //路径跳转
        goNext(link, item) {
            this.$router.push({
                path: link,
                query: {
                    accountUnitId: item.id,
                    hotelId: this.$route.query.hotelId,
                    cooperationName: item.cooperationName,
                    contact: item.contact,
                    phone: item.phone,
                    unpayPrice: item.unpayPrice
                }
            });
        }
    }
};
</script>

<style scoped>
.new-business {
    height: 2.4rem;
    background: #fff;
    font-size: 14px;
    line-height: 2.4rem;
    color: #000;
    margin: 6px 0;
}
.business-group {
    overflow: auto;
    position: fixed;
    left: 0;
    right: 0;
    height: 75%;
    top: 160px;
    font-size: 14px;
}
.business-item {
    background: #fff;
    padding: 10px;
    margin: 2px 0;
    line-height: 25px;
}
</style>



